//all color used in the page
$main-color: #014e58;
$second-color: #032e34;
$alt-color:#f67e7f;

$p-color: #ddd;
$h-color: #fff;
// for fonts 
$main-font: 16px;


// start varibes 
%main-div{
    position: relative;
    margin-top:0;
    margin-left: 0;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    
}
%over{
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($main-color, .8);
}
%button{
   margin: 50px auto;
	border: 1px solid $h-color;
	width: 120px;
	padding: 8px;
	background-color: transparent;
	color: $h-color;
	outline: none;
	cursor: pointer;
	
}

@mixin transition($t){
     -webkit-transition:all $t ease-in-out;
     -moz-transition: all $t ease-in-out;
     -ms-transition:all $t ease-in-out;
     -o-transition:all $t ease-in-out;
     transition:all $t ease-in-out;
}

@mixin border-R($b){
     -webkit-border-radius:$b;
     -moz-border-radius:$b;
     -ms-border-radius:$b;
     -o-border-radius:$b;
     border-radius:$b;
    }


// this for scrollbar
::-webkit-scrollbar {
    width: 8px;
}
::-webkit-scrollbar-track {
    background: $second-color; 
     border:none;
    background-color: transparent;
}
::-webkit-scrollbar-thumb {
    background: $main-color; 
}
::-webkit-scrollbar-thumb:hover {
    background: $second-color; 
    @include transition(1s);
}